//:::::::::::Imports: :::::::::://
@import '../_config/imports';

//:::::::::::Styles::::::::::://

.tags, .tag {
	@include font-roboto;
}

// this styles for prism highlight code
.tag {
	&.token {
		font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
		background-color: transparent;
		font-size: 14.5px;
		padding: 0;
		display: inline;
	}
}